.App {
  text-align: left;
  width: 100%;
  box-sizing: border-box;
}
.header{
  padding: 20px 5%;
  text-align: center;
  width: 100%;
  /* background-color: saddlebrown; */
  display: flex;
  color: black;
  justify-content: space-between;
  align-items: center

}
.search{
  height: 30px;
  width: 20%;
  padding: 10px;
  border-radius: 5px;
  font-size: 1rem;
  outline: none;
  border: 1px solid gray;

}
.news-articles{
  /* width: 100%; */
  height: fit-content;
  background-color: #f2f2f2;
  padding: 20px 5%;
  display: flex;
  flex-wrap: wrap;
  column-gap: 4%;
  row-gap: 10px;

}
.news{
  
  width: 22%;
  margin: 10px 0px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  padding: 30px;
  background-color:rgba(255, 255, 255, 0.92);
}

.news-img{
  height: 200px;
  width: 100%;
  overflow: hidden;
  /* background-color: rgb(136, 136, 136); */
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.news-img img{
  /* height: 100%; */
  width: 100%;
  /* max-height: 100%; */
  
}

